import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, DatePicker } from 'antd';
import { updatePoliticalDate } from 'services/studentForPartyMem';

@Form.create({ name: 'FormStuPartyMem' })
class FormStuPartyMem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      studentId: null,
    };
  }

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ studentId: match.params.id });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const dateValue = {
        ...values,
        'date-picker': values['date-picker'].format('YYYY-MM-DD'),
      };
      this.saveData(dateValue['date-picker']);
    });
  };

  saveData = async dateString => {
    const { studentId } = this.state;
    try {
      await updatePoliticalDate({ id: studentId, politicalDate: dateString });
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const config = {
      rules: [{ type: 'object', message: 'Vui lòng chọn ngày!', required: true }],
    };
    return (
      <Card title="">
        <Row>
          <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSubmit}>
            <Form.Item label="Từ ngày" {...formItemLayout} hasFeedback>
              {getFieldDecorator('date-picker', config)(<DatePicker />)}
            </Form.Item>
            <div className="btn-group">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </div>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormStuPartyMem {...props} />
  </Page>
));



