import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getStudentsForPartyMem = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  studentCode,
  studentFullName,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/Student/GetPoliticStudents',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      studentCode,
      studentFullName,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const updatePoliticalDate = ({ id, politicalDate }) =>
  request({
    prefix,
    url: `/Student/${id}/UpdatePoliticalDate`,
    params: {},
    data: { ids: [id], politicalDate },
    method: 'PUT',
  });

export const exportPoliticStudents = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  studentCode,
  studentFullName,
}) =>
  requestDownload({
    defaultFileName: 'fileDownload.xlsx',
    prefix,
    url: `/Student/ExportPoliticStudents`,
    method: 'POST',
    data: { trainTypeId, facultyId, courseId, studyFieldId, classId, studentCode, studentFullName },
  });



